// 计算两个点的距离
export const SpotDistance = (latOneX, lngOneY, latTwoX, lngTwoY) => {
  var s = null
  var x1 = eval(latOneX)
  var y1 = eval(lngOneY)
  var x2 = eval(latTwoX)
  var y2 = eval(lngTwoY)
  var calX = x2 - x1
  var calY = y2 - y1
  s = Math.pow((calX * calX + calY * calY), 0.5)
  return s
}
// 计算两个线的相交
export const lineIntersect = (a, b, c, d) => {
/** 1 解线性方程组, 求线段交点. **/
// 如果分母为0 则平行或共线, 不相交
  var denominator = (b.y - a.y) * (d.x - c.x) - (a.x - b.x) * (c.y - d.y)
  if (denominator === 0) {
    return false
  }

  // 线段所在直线的交点坐标 (x , y)
  var x = ((b.x - a.x) * (d.x - c.x) * (c.y - a.y) +
            (b.y - a.y) * (d.x - c.x) * a.x -
            (d.y - c.y) * (b.x - a.x) * c.x) / denominator
  var y = -((b.y - a.y) * (d.y - c.y) * (c.x - a.x) +
            (b.x - a.x) * (d.y - c.y) * a.y -
            (d.x - c.x) * (b.y - a.y) * c.y) / denominator

  /** 2 判断交点是否在两条线段上 **/
  if (
    // 交点在线段1上
    (x - a.x) * (x - b.x) <= 0 && (y - a.y) * (y - b.y) <= 0 &&
    // 且交点也在线段2上
     (x - c.x) * (x - d.x) <= 0 && (y - c.y) * (y - d.y) <= 0
  ) {
    // 返回交点p
    return {
      x: x,
      y: y
    }
  }
  // 否则不相交
  return false
}
// 判断两个矩形是否重叠
export const isOverlap = (rect1, rect2) => {
  const l1 = { x: rect1.x, y: rect1.y }
  const r1 = { x: rect1.x + rect1.width, y: rect1.y + rect1.height }
  const l2 = { x: rect2.x, y: rect2.y }
  const r2 = { x: rect2.x + rect2.width, y: rect2.y + rect2.height }
  if (
    l1.x > r2.x ||
    l2.x > r1.x ||
    l1.y > r2.y ||
    l2.y > r1.y
  ) return false
  return true
}
