import React, { useEffect, useState, useRef, useContext } from 'react'
import ReactDOM from 'react-dom'
import './revoke.css'
import returnPress from '../assets/return-pressed.svg'
import returnNormal from '../assets/return-normal.svg'
import rotateNormal from '../assets/rotate-normal.svg'
import dataRevoke from '.././utils/revokeLayer'
import IsAgainEdit from './isAgainEdit'
import { LocaleContext } from '../contexts/LocaleContext'
import Locale from '../components/Locale'

export default function revoke (props) {
  const [show, setShow] = useState(false)
  const languageType = useContext(LocaleContext)
  const [isAgainEdit, setIsAgainEdit] = useState(false)
  useEffect(() => {
    setShow(props.show)
  }, [props.show])

  function revokeDesign (param) {
    setShow(false)
    if (param === 'current') {
      if (props.cancelDesign) {
        props.cancelDesign()
        props.setShow(false)
      }
      // dataRevoke()
    } else {
      setIsAgainEdit(true)
      setTimeout(() => {
        setIsAgainEdit(true)
      }, 100)
    //   dataRevoke(props.pageMark)
    }
  }

  function isAgainFu (e) {
    setIsAgainEdit(false)
    if (props.setShow) {
      props.setShow(false)
    }
  }

  return (
    <>
      <div className='revokeMask' style={{ display: show ? 'block' : 'none' }} onClick={() => {
        props.setShow(false)
        setTimeout(() => {
          props.setShow(false)
        }, 100)
      }} />
      <div style={{
        width: '100%',
        // maxHeight: 160,
        backgroundColor: '#fff',
        position: 'fixed',
        zIndex: 21,
        bottom: show ? 0 : -194,
        fontSize: languageType.current === 'ja' ? 17 : 20,
        fontWeight: 500,
        transition: 'bottom 0.2s'
      }}>
        <div className='revokeOperation' onClick={() => revokeDesign('current')}>
          <div className='revokeOpImg' >
            <img src={rotateNormal} />
          </div>
          <span style={{ fontWeight: languageType.current === 'ja' ? '' : 'bold', fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'HelveticaNeue-Bold' }}>
            {/* 重设图片 */}
            {
              props.pageMark === 'mix'
                ? <Locale code='make.revoke.resetEffect' />
                : <Locale code='make.revoke.resetGraphic' />
            }
          </span>
        </div>
        <div className='revokeOperation' onClick={() => revokeDesign('initial')}>
          <div className='revokeOpImg'>
            <img src={returnNormal} />
          </div>
          <span style={{ fontWeight: languageType.current === 'ja' ? '' : 'bold', fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'HelveticaNeue-Bold' }}>
            {/* 回到初始状态 */}
            <Locale code='make.revoke.BackToTheOriginalState' />
          </span>
        </div>
        <div style={{ height: 52, padding: 15 }}>
          <button style={{
            height: 52,
            lineHeight: '52px',
            // lineHeight: ,
            letterSpacing: 0.4,
            // marginTop: 7,
            width: '100%',
            // width: window.innerWidth - 16,
            // marginLeft: 8,
            // fontWeight: 500,
            border: '1px solid #1b1b1b',
            fontSize: 16,
            backgroundColor: 'white',
            fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'UniqloPro-Bold'
          }} onClick={() => props.setShow(false)}>
            {/* 取消 */}
            <Locale code='make.revoke.cancel' />
          </button>
        </div>
      </div>
      <IsAgainEdit resetLayers={props.resetLayers} status={isAgainEdit} isAgainFu={isAgainFu} />
      </>
  )
}
