/**
 * stamp临时存储地方
 */
var newStamp = []
// 保存贴图
export const setStampUrl = (e) => {
  if (e) {
    newStamp.push(e)
  }
}
// 获取贴图
export const getStampUrl = (id) => {
  var activeBase = {}
  var activeType = false
  if (newStamp) {
    if (newStamp.length > 0) {
      for (let i = 0; i < newStamp.length; i++) {
        if (newStamp[i].id === id) {
          activeBase = newStamp[i]
          activeType = true
        }
      }
    }
  }
  return {
    activeBase: activeBase,
    activeType: activeType
  }
}
// 查看是否存储数据
export const stateList = (e) => {
  if (newStamp) {
    if (newStamp.length > 0) {
      return true
    } else {
      return false
    }
  } else {
    return false
  }
}
// 删除全部贴图
export const clearBaseUrls = () => {
  newStamp = []
}
// 获取全部贴图
export const allBaseUrl = () => {
  return newStamp
}
export default {
  setStampUrl,
  getStampUrl,
  stateList,
  clearBaseUrls,
  allBaseUrl
}
