  async function passUrl (e) {
    const url = e.stamp_image_url // 最大尺寸的照片
    // const url = e.stamp_thumbnail_image_url // 中等尺寸的照片
    // const url = e.stamp_list_thumbnail_image_url // 最小尺寸的照片
    close()
    // Loading({ type: true })
    // props.onSelectStamp(url, e.id, manData)
    var baseType = false
    Loading({ type: true })
    try {
      var data1 = stateList()
      if (data1) {
        const getData = getStampUrl(e.id)
        if (getData) {
          if (getData.activeType) {
            baseType = true
            if (getData.activeBase) {
              let res = getData.activeBase
              await props.onSelectStamp(res.baseUrl, res.id, manData, e)
              Loading({ type: false })
            }
          }
        }
        if (!baseType) {
          Loading({ type: true })
          const baseUrl = await invoke('downloadFile', url)
          if (baseUrl.image) {
            const stampUrl = {
              id: e.id,
              baseUrl: baseUrl.image
            }
            // data1.push(stampUrl)
            // sessionStorage.setItem('stampUrlArry', JSON.stringify(data1))
            setStampUrl(stampUrl)
            await props.onSelectStamp(baseUrl.image, e.id, manData, e)
            Loading({ type: false })
          }
        }
      } else {
        const baseUrl = await invoke('downloadFile', url)
        if (baseUrl.image) {
          const stampUrl = {
            id: e.id,
            baseUrl: baseUrl.image
          }
          try {
            setStampUrl(stampUrl)
          } catch {
            alert('未能获取，请重试！')
          }
          await props.onSelectStamp(baseUrl.image, e.id, manData, e)
          Loading({ type: false })
        }
      }
    } catch (e) {
      Loading({ type: false })
    }
  }
  function t_switch (e) {
    const looseCoat = document.getElementById('looseCoat')
    const tableOne = document.getElementById('tableOne')
    const tableTwo = document.getElementById('tableTwo')
    if (e === 'one') {
      tableOne.style.color = '#1B1B1B'
      tableOne.style.backgroundColor = '#FFFFFF'
      tableTwo.style.color = '#FFFFFF'
      tableTwo.style.backgroundColor = '#ABABAB'
      looseCoat.style.left = 0 + 'px'
    }

    if (e === 'two') {
      /**
       * 判断禁止模块儿是否显示
       */
      if (manData.flag_used_with_multiple_stamps === 'OK' && manData.flag_used_with_same_stamps === 'OK' && manData.flag_rotation === 'OK' && manData.flag_can_over === 'OK') {
        const htm = document.getElementById('restrictionPromptContentOneModel')
        if (htm) {
          htm.remove()
        }
      }
      if (manData.flag_layer_stamp === 'OK' && manData.flag_layer_paint === 'OK' && manData.flag_layer_text === 'OK' && manData.flag_layer_photo === 'OK') {
        const htm = document.getElementById('restrictionPromptContentTwoModel')
        if (htm) {
          htm.remove()
        }
      }
      if ((manData.flag_effect_moma01 === 'OK' || manData.flag_effect_moma01 === 'LOOSE_NG') && (manData.flag_effect_moma02 === 'OK' || manData.flag_effect_moma02 === 'LOOSE_NG') && (manData.flag_effect_layout === 'OK' || manData.flag_effect_layout === 'LOOSE_NG') && (manData.flag_effect_splash === 'OK' || manData.flag_effect_splash === 'LOOSE_NG') && (manData.flag_effect_glitch === 'OK' || manData.flag_effect_glitch === 'LOOSE_NG') && (manData.flag_effect_mosaic === 'OK' || manData.flag_effect_mosaic === 'LOOSE_NG')) {
        const htm = document.getElementById('restrictionPromptContentThreeModel')
        if (htm) {
          htm.remove()
        }
      }
      if (manData.flag_can_other_layer_over === 'OK' && manData.flag_list === 'OK') {
        const htm = document.getElementById('restrictionPromptContentFourModel')
        if (htm) {
          htm.remove()
        }
      }
      tableOne.style.color = '#FFFFFF'
      tableOne.style.backgroundColor = '#ABABAB'
      tableTwo.style.color = '#1B1B1B'
      tableTwo.style.backgroundColor = '#FFFFFF'
      looseCoat.style.left = -window.innerWidth + 'px'
    }
  }
