import React, { useContext, useEffect, useRef, useState } from 'react'
import { MakeContext } from '../../MakeContext'
import Navbar from '../../navbar'
import invoke from '../../../utils/invoke'
import stamp from '../../../assets/stamp.png'
import { fabric } from '../../../utils/fabric'
import GotoMixIcon from '../../../assets/Next_Normal.svg'
import { ItemContext } from '../../../contexts/ItemContext'
import Locale from '../../../components/Locale'
import { ljh_popups } from '../../../components/ljhPopups'
import '../../../components/ljhPopups.css'
import './setStamp.css'
import AddMap from './addMap/addMap'
import {
  isOverlap,
  LimitDeleteIcon,
  Multiple,
  Regression,
  SpotDistance,
  RotatedRectang
} from './pageAlgorithm'
import deleteImg from '../../../assets/DeleteStampIcon.svg'
import RevokeComponent from '../../../components/revoke'
import { $mapEmit, $slideEmit } from '../../../components/observer'
import nextDisabel from '../../../assets/next_disabel.svg'
import ViewPort from '../../../components/ViewPort'
import Toolbar from '../../toolbar'
import Enlarge from '../../../components/Enlarge'
import { Loading } from '../../../components/loading'
import editArea from '../../../assets/editArea.png'
import { LocaleContext } from '../../../contexts/LocaleContext'
import TypesetPrompt from '../../../components/TypesetPrompt'
import instruction from '../../../utils/instruction'
import constants from '../../../constants/instruction'
import injectImages from '../../../hoc/injectImages'
import textBoxIconBackground from '../../../assets/textBoxIconBackground.svg'
import fixFabricSize from '../../../utils/fixFabricSize'
import AlertModal from '../../../components/AlertModal'
// import Filters from './glowFileter'
import imageTools from '../../../utils/imageTools'
var firstSlectMap = true
// var MapData = {}
var firstSignNum = 0
var nums = 0
var imgOverFlow = false
var flag_can_other_layer_over = ''
var delay = null
var delayType = true
var overLap = null
var lastTop = null
var lastLeft = null
var scaleType = ''
var scaleNumner = null
function SetStamp (props) {
  const make = useContext(MakeContext)
  const item = useContext(ItemContext)
  const [canvas, setCanvas] = useState()
  const { imageSize, scale, dynamicDeviceWidth, dynamicDeviceHeight, areaStyle, canvasArea, canvasSize } = item
  const [enlargeAttr, setEnlargeAttr] = useState('big')
  const [jiuVisible, setJiuVisible] = useState(false)
  const [deleteIconWidth, setDeleteIconWidth] = useState(30)
  const [deleteIconHeight, setDeleteIconHeight] = useState(42)
  const [stampSetArry, setStampSetArry] = useState([])
  const [showStatus, setShowStatus] = useState(false)
  const local = useContext(LocaleContext)
  const [showTypeset, setShowTypeset] = useState(false)
  const [textType, setTextType] = useState(false)
  const [maskType, setMaskType] = useState(true)
  const [MapData, setMapData] = useState({})
  const [alertModelVisible, setAlertModelVisible] = useState(false)
  const fontComponent = useRef(null)
  const canvasRef = useRef()
  const GotoMixIconRef = useRef(null)
  const nextDisabelRef = useRef(null)
  const deleteImgRef = useRef(null)
  const addMapAlertRef = useRef(null)
  const deleteObj = useRef(null)
  const {
    layers,
    previousPage
  } = make
  const layer = layers ? layers[layers.length - 2] : null

  function goStampShowcase () {
    fontComponent.current()
  }
  useEffect(() => {
    const { current } = canvasRef
    if (current) {
      setCanvas(new fabric.Canvas(current, { enableRetinaScaling: false }))
    }
  }, [canvasRef])
  useEffect(() => {
    lastTop = canvasArea.top
    lastLeft = canvasArea.left
    return () => {
      initialization()
    }
  }, [])
  useEffect(() => {
    window.getSelection ? window.getSelection().removeAllRanges() : document.selection.empty()
    if (canvas) {
      canvas.selection = false
    }
  }, [canvas])

  useEffect(() => {
    if (scaleNumner === null) {
      scaleType = 'small'
      scaleNumner = canvasSize * scale
    } else {
      if (parseInt(scaleNumner) !== parseInt(canvasSize * scale)) {
        scaleType = 'large'
      } else {
        scaleType = 'small'
      }
    }

    // item切换时位置调整
    if (canvas) {
      canvas.setWidth(canvasSize)
      canvas.setHeight(canvasSize)
      fixFabricSize(canvas, imageSize, imageSize)
      if (parseInt(lastTop) !== parseInt(canvasArea.top)) {
        if (canvas) {
          if (canvas.getObjects().length !== 0) {
            for (let i = 0; i < canvas.getObjects().length; i++) {
              const data = canvas.getObjects()[i]
              data.set({
                left: (data.left - lastLeft) + canvasArea.left,
                top: (data.top - lastTop) + canvasArea.top
              }).setCoords()
            }
            canvas.renderAll()
          }
        }
        lastTop = canvasArea.top
        lastLeft = canvasArea.left
      }
    }
  }, [canvasSize, canvasArea])
  useEffect(() => {
    if (canvas) {
      function bindingDown (e) {
        if (canvas.getObjects().length > 0) {
          setJiuVisible(true)
        }
        // 点击编辑区域时取消重叠导致的置红
        if (canvas.getObjects().length > 0) {
          for (let i = 0; i < canvas.getObjects().length; i++) {
            const data = canvas.getObjects()[i]
            if (data.filters.length > 0) {
              data.filters = []
              data.applyFilters()
            }
          }
        }
        canvas.renderAll()
        if (e.target) {
          const data = e.target
        }
      }

      function bindingScaling (e) {
        if (e.target) {
          const data = e.target
          const getData = Multiple()
          // 放大或缩小时限制贴图的最大比例及最小比例
          if (data.scaleX > getData.maxScale) {
            data.set({
              scaleX: getData.maxScale,
              scaleY: getData.maxScale
            }).setCoords()
          } else if (data.scaleX < getData.minScale) {
            data.set({
              scaleX: getData.minScale,
              scaleY: getData.minScale
            }).setCoords()
          }
        }
      }

      function bindingRotating (e) {
        if (e.target) {
          const data = e.target
          const centerLeft = parseInt(data.left)
          const centerTop = parseInt(data.top)
          // const optionsLeft = (canvasSize - canvasArea.width) / 2;
          const angle = parseInt(data.angle)
          const htmDeleteImg = deleteImgRef.current
          if (MapData.flag_rotation === 'INVERT_NG') {
            if (angle >= 45 && angle > 0) {
              data.set({
                angle: 45,
                left: centerLeft,
                top: centerTop
              }).setCoords()
            }
            if (angle <= -45) {
              data.set({
                angle: -45,
                left: centerLeft,
                top: centerTop
              }).setCoords()
            }
          }
          // 实时更新删除图标显示的位置
          data.setCoords()
          const params = {
            scaleType: scaleType,
            canvasSize: canvasSize,
            scale: scale,
            dynamicDeviceWidth: dynamicDeviceWidth,
            dynamicDeviceHeight: dynamicDeviceHeight,
            deleteIconWidth: deleteIconWidth,
            deleteIconHeight: deleteIconHeight,
            data: data,
            areaStyle: areaStyle,
            canvasArea: canvasArea,
            canvas: canvas
          }
          const limitData = LimitDeleteIcon(params)
          if (limitData) {
            htmDeleteImg.style.left = (limitData.left) + 'px'
            htmDeleteImg.style.top = limitData.top + 'px'
          }
        }
      }

      function bindingRotated (e) {
        if (e.target) {
          const data = e.target
          operationRuleBoundary(data)
        }
      }

      function bindingMoving (e) {
        if (e.target) {
          const data = e.target
          const centerLeft = parseInt(data.left)
          const centerTop = parseInt(data.top)
          const optionsLeft = (canvasSize - canvasArea.width) / 2
          const htmDeleteImg = deleteImgRef.current
          if (centerLeft < optionsLeft || centerTop > (canvasArea.top + canvasArea.height) || centerLeft > (canvasArea.left + canvasArea.width) || centerTop < canvasArea.top) {
            data.set({ opacity: 0.3 }).setCoords()
            canvas.renderAll()
            imgOverFlow = true
            htmDeleteImg.style.display = 'block' // 触摸时显示图标
          } else {
            data.set({ opacity: 1 }).setCoords()
            canvas.renderAll()
            imgOverFlow = false
            htmDeleteImg.style.display = 'none' // 触摸时显示图标
          }
          // 实时更新删除图标显示的位置
          const params = {
            scaleType: scaleType,
            canvasSize: canvasSize,
            scale: scale,
            dynamicDeviceWidth: dynamicDeviceWidth,
            dynamicDeviceHeight: dynamicDeviceHeight,
            deleteIconWidth: deleteIconWidth,
            deleteIconHeight: deleteIconHeight,
            data: data,
            areaStyle: areaStyle,
            canvasArea: canvasArea,
            canvas: canvas
          }
          const limitData = LimitDeleteIcon(params)
          if (limitData) {
            htmDeleteImg.style.left = (limitData.left) + 'px'
            htmDeleteImg.style.top = limitData.top + 'px'
          }
        }
      }

      function bindingUp (e) {
        setJiuVisible(false)
        const htmDeleteImg = deleteImgRef.current
        const GotoMixIconHTML = GotoMixIconRef.current
        const nextDisabel = nextDisabelRef.current
        const addMapAlert = addMapAlertRef.current
        if (htmDeleteImg) {
          htmDeleteImg.style.display = 'none' // 触摸结束时删除图标
        }
        if (e.target) {
          const data = e.target
          // 当移动完之后判断是否溢出，溢出状态下删除此贴图
          if (imgOverFlow) {
            deleteObj.current(data)
            canvas.remove(data)
            imgOverFlow = false
            if (stampSetArry) {
              if (stampSetArry.length > 0) {
                for (let z = 0; z < stampSetArry.length; z++) {
                  if (data.id === stampSetArry[z]) {
                    if (nums === 0) {
                      nums++
                      let newArry = []
                      newArry = stampSetArry.splice(z, 1)
                      setStampSetArry(newArry)
                    }
                  }
                }
              }
            }
            setStampSetArry(stampSetArry)
            nums = 0
            // firstSignNum = 0 // 当图片删除时初始化值
            // 判断画布上是否有图层，如果没有的话把右上角的图标置灰
            if (canvas.getObjects().length === 0) {
              setTextType(false)
              // 添加图标提示dom
              addMapAlert.style.display = 'block'
              // 右上角的图标置灰图标dom
              GotoMixIconHTML.style.display = 'none'
              nextDisabel.style.display = 'block'
            } else {
              GotoMixIconHTML.style.display = 'block'
              nextDisabel.style.display = 'none'
            }
          }
          rotatedLimitWidth({
            data: data,
            centerLeft: parseInt(data.left),
            centerTop: parseInt(data.top),
            optionsLeft: (canvasSize - canvasArea.width) / 2,
            areaStyle: canvasArea,
            canvasSize: canvasSize
          })
          firstSignNum = 0
        }
      }
      function touchcancel (e) {
        console.log('系统默认事件触发了')
        setJiuVisible(false)
        setMaskType(true)
        const htmDeleteImg = deleteImgRef.current
        if (htmDeleteImg) {
          htmDeleteImg.style.display = 'none' // 触摸结束时删除图标
        }
      }
      const can = canvas.upperCanvasEl
      canvas.on('mouse:down', bindingDown, false)
      canvas.on('object:scaling', bindingScaling, false)
      canvas.on('object:rotating', bindingRotating, false)
      canvas.on('object:rotated', bindingRotated, false)
      canvas.on('object:moving', bindingMoving, false)
      canvas.on('mouse:up', bindingUp, false)
      can.addEventListener('touchcancel', touchcancel, false)
      return () => {
        canvas.off('mouse:down', bindingDown, false)
        canvas.off('object:scaling', bindingScaling, false)
        canvas.off('object:rotating', bindingRotating, false)
        canvas.off('object:rotated', bindingRotated, false)
        canvas.off('object:moving', bindingMoving, false)
        canvas.off('mouse:up', bindingUp, false)
        can.removeEventListener('touchcancel', touchcancel, false)
      }
    }
  }, [canvas, canvasArea])
  useEffect(() => {
    if (canvas) {
      return () => {
        canvas.dispose()
      }
    }
  }, [canvas])
  async function onSelectStamp (e, k, manData, currentData) {
    if (!showTypeset) {
      try {
        let visited = await instruction.checkGlobalInstruction(constants.Stamp_Resize_Instruction)
        if (!visited) {
          setShowTypeset(true)
          await instruction.setGlobalInstructionVisited(constants.Stamp_Resize_Instruction)
        }
      } catch (error) {
        setAlertModelVisible(true)
        let visited = await instruction.checkGlobalInstruction(constants.Stamp_Resize_Instruction)
        if (!visited) {
          setShowTypeset(true)
          await instruction.setGlobalInstructionVisited(constants.Stamp_Resize_Instruction)
        }
      }
    } else {
      setShowTypeset(false)
    }
    const GotoMixIconHTML = GotoMixIconRef.current
    const nextDisabel = nextDisabelRef.current
    if (canvas) {
      fixFabricSize(canvas, imageSize, imageSize)
      if (e) {
        // 加载失败的贴图不进行下一步操作
        if (currentData.stamplist_image_type) {
          Loading({ type: false })
          item.setZoom(true)
          setAlertModelVisible(true)
          return
        }
        // 存储数据
        setMapData(manData)
        // 隐藏添加贴图提示
        const addMapAlert = addMapAlertRef.current
        if (addMapAlert) {
          addMapAlert.style.display = 'none'
        }
        // 循环遍历重置所有贴图的优先级(显示层级)
        if (canvas.getObjects().length > 0) {
          canvas.discardActiveObject()
          canvas.requestRenderAll()
        }
        // 取消canvas组选器
        canvas.selection = false

        // 判断这套贴图是否重复使用
        if (manData.flag_used_with_multiple_stamps === 'NG') {
          if (canvas.getObjects().length > 0) {
            canvas.remove(canvas.getObjects()[0])
          }
        }
        fabric.Image.fromURL(e, function (img) {
          // 判断此图是否旋转以及能够旋转的角度
          if (manData.flag_rotation === 'NG') {
            img.set({
              lockRotation: true
            }).setCoords()
          }

          // 判断是否把本此选择的图片展示在最上层
          if (manData.flag_can_other_layer_over === 'NG') {
            flag_can_other_layer_over = 'NG'
          }
          // 给贴图添加白边
          if (manData.flag_fringe === 'OK') {
            console.log('给贴图添加白边')
            // const test = Filters.getPixels(e, img.width, img.height)
            // console.log(test)
          }
          // 给img对象赋值原有的id 以及 状态
          img.id = k
          img.flag_used_with_multiple_stamps = manData.flag_used_with_multiple_stamps
          img.flag_used_with_same_stamps = manData.flag_used_with_same_stamps
          const getData = Multiple()
          if (getData.beishu) {
            img.set({
              scaleX: getData.beishu,
              scaleY: getData.beishu
            })
          }
          img.set({
            left: parseInt(canvasArea.left + (canvasArea.width / 2)),
            top: parseInt(canvasArea.top + (canvasArea.height / 2)),
            padding: 0,
            hasControls: false,
            hasBorders: false,
            hasRotatingPoint: false,
            centeredRotation: true,
            centeredScaling: true,
            centerTransform: true,
            // cornerSize: 60,
            // transparentCorners: false,
            originX: 'center',
            originY: 'center'
          }).setCoords()

          img.saveState()
          // 把贴图添加到canvas画布上
          canvas.add(img)
          canvas.renderAll()

          const ifObjtects = canvas.getObjects()
          if (ifObjtects.length > 0) {
            Loading({ type: false })
            setTextType(true)
            firstSignNum = 0
            // 收集贴图id
            if (manData.flag_used_with_multiple_stamps === 'NG') {
              let newStampSetArry = []
              newStampSetArry.push(k)
              setStampSetArry(newStampSetArry)
            } else {
              stampSetArry.push(k)
              setStampSetArry(stampSetArry)
            }
            if (firstSlectMap) {
              firstSlectMap = false
              setTimeout(() => {
                item.setZoom(true)
              }, 300)
            }
            GotoMixIconHTML.style.display = 'block'
            nextDisabel.style.display = 'none'
          }
        })
      }
    }
  }
  function rotatedLimitWidth (res) {
    var moveWidth = null
    var moveHeight = null
    const data = res.data
    const routatedObj = RotatedRectang(res)
    const centerLeft = parseInt(data.left)
    const centerTop = parseInt(data.top)
    const optionsLeft = (canvasSize - canvasArea.width) / 2
    const options = {
      data: data,
      centerLeft: centerLeft,
      centerTop: centerTop,
      optionsLeft: optionsLeft,
      areaStyle: canvasArea,
      canvasSize: canvasSize
    }
    const regress = Regression(options)
    if (regress) {
      if (regress.type !== 'no') {
        setMaskType(false)
      }
    }
    if (routatedObj) {
      moveWidth = routatedObj.scalex
      moveHeight = routatedObj.scaley
    }
    const positionOptions = {
      scaleX: moveWidth === null ? data.scaleX : moveWidth,
      scaleY: moveHeight === null ? data.scaleY : moveHeight
    }
    const time0ptions = {
      duration: 100,
      onChange: canvas.renderAll.bind(canvas)
    }
    data.animate(positionOptions, time0ptions).setCoords()
    setTimeout(() => {
      operationRuleBoundary(data, 'diff')
    }, 200)
  }
  function operationRuleBoundary (data, diff) {
    const centerLeft = parseInt(data.left)
    const centerTop = parseInt(data.top)
    const optionsLeft = (canvasSize - canvasArea.width) / 2
    const options = {
      data: data,
      centerLeft: centerLeft,
      centerTop: centerTop,
      optionsLeft: optionsLeft,
      areaStyle: canvasArea,
      canvasSize: canvasSize
    }
    const regress = Regression(options)
    if (regress.type !== 'no') {
      var moveLeft = null
      var moveTop = null
      if (regress.left) {
        moveLeft = regress.left
      }
      if (regress.top) {
        moveTop = regress.top
      }
      const positionOptions = {
        left: moveLeft === null ? data.left : moveLeft,
        top: moveTop === null ? data.top : moveTop
      }
      const time0ptions = {
        duration: 300,
        onChange: canvas.renderAll.bind(canvas),
        easing: fabric.util.ease.easeOutBounce,
        onComplete: () => {
          setMaskType(true)
        }
      }
      data.animate(positionOptions, time0ptions).setCoords()
    } else {
      setTimeout(() => {
        setMaskType(true)
      }, 300)
    }
  }

  function change () {
    setShowStatus(!showStatus)
  }

  function cancelDesign () {
    if (canvas) {
      if (canvas.getObjects().length !== 0) {
        canvas.clear()
        const GotoMixIconHTML = GotoMixIconRef.current
        const nextDisabel = nextDisabelRef.current
        GotoMixIconHTML.style.display = 'none'
        nextDisabel.style.display = 'block'
        deleteObj.current({ type: 'reset' })
      }
    }
  }

  async function toJson () {
    try {
      var overType = false
      const { current } = canvasRef
      const htm = current
      const left = parseInt(canvasArea.left)
      const top = parseInt(canvasArea.top)
      const width = parseInt(canvasArea.width)
      const height = parseInt(canvasArea.height)
      var temporaryCanvas = document.createElement('canvas')
      temporaryCanvas.width = width
      temporaryCanvas.height = height
      var ctx = temporaryCanvas.getContext('2d')
      var imgNum = 16
      for (let i = 0; i < imgNum; i++) {
        if (i < 4) {
          const imageData = htm.getContext('2d').getImageData(left + (i * (width / (imgNum / 4))), top, width / (imgNum / 4), height / (imgNum / 4))
          ctx.putImageData(imageData, i * (width / (imgNum / 4)), 0)
        } else if (i > 3 && i <= 7) {
          const imageData = htm.getContext('2d').getImageData(left + ((i - 4) * (width / (imgNum / 4))), top + (height / (imgNum / 4)), width / (imgNum / 4), height / (imgNum / 4))
          ctx.putImageData(imageData, (i - 4) * (width / (imgNum / 4)), (height / (imgNum / 4)))
        } else if (i > 7 && i <= 11) {
          const imageData = htm.getContext('2d').getImageData(left + ((i - 8) * (width / (imgNum / 4))), top + 2 * (height / (imgNum / 4)), width / (imgNum / 4), height / (imgNum / 4))
          ctx.putImageData(imageData, (i - 8) * (width / (imgNum / 4)), 2 * (height / (imgNum / 4)))
        } else if (i > 11 && i <= 15) {
          const imageData = htm.getContext('2d').getImageData(left + ((i - 12) * (width / (imgNum / 4))), top + 3 * (height / (imgNum / 4)), width / (imgNum / 4), height / (imgNum / 4))
          ctx.putImageData(imageData, (i - 12) * (width / (imgNum / 4)), 3 * (height / (imgNum / 4)))
        }
      }
      // const imageData = htm.getContext('2d').getImageData(left, top, width, height)
      // ctx.putImageData(imageData, 0, 0)
      // base64  截取图片
      const toDataurl = temporaryCanvas.toDataURL()
      // 判断图层是否重叠
      if (MapData.flag_can_over === 'NG' || MapData.flag_can_over === 'LOOSE_NG') {
        if (canvas.getObjects().length > 0) {
          if (canvas.getObjects().length > 1) {
            if (canvas.getObjects().length >= 2) {
              for (let j = 0; j < canvas.getObjects().length; j++) {
                const data = canvas.getObjects()[j]
                for (let z = 0; z < canvas.getObjects().length; z++) {
                  const childData = canvas.getObjects()[z]
                  if (data.id !== childData.id) {
                    var mapOneWidth = data.scaleX * data.width
                    var mapOneHeight = data.scaleY * data.height
                    var mapOneLeft = data.left - (mapOneWidth / 2)
                    var mapOneTop = data.top - (mapOneHeight / 2)

                    var mapTwoWidth = childData.scaleX * childData.width
                    var mapTwoHeight = childData.scaleY * childData.height
                    var mapTwoLeft = childData.left - (mapTwoWidth / 2)
                    var mapTwoTop = childData.top - (mapTwoHeight / 2)

                    var rect1 = { x: mapOneLeft, y: mapOneTop, width: mapOneWidth, height: mapOneHeight }
                    var rect2 = { x: mapTwoLeft, y: mapTwoTop, width: mapTwoWidth, height: mapTwoHeight }

                    overLap = isOverlap(rect1, rect2)
                    if (overLap) {
                      overType = true
                      if (data.filters.length === 0) {
                        const f = fabric.Image.filters
                        const fil = new f.ColorMatrix({
                          matrix: [
                            1, 0, 0, 0, 0,
                            0, 0.5, 0, 0, 0,
                            0, 0, 0.5, 0, 0,
                            0, 0, 0, 1, 0
                          ]
                        })
                        data.filters.push(fil)
                        data.applyFilters()
                      }
                    }
                  }
                }
              }
              canvas.renderAll()
            }
          }
        }
        if (overType) {
          if (local) {
            const lang = local.current
            ljh_popups({
              type: 'overlap',
              content: lang === 'ja' ? 'このスタンプは重ねて配置できません' : 'This sticker can\'t be overlapped',
              describe: lang === 'ja' ? 'スタンプ同士が重ならないよう移動させてください。' : 'Please adjust the stickers so they don\'t overlap each other.',
              next_text: lang === 'ja' ? 'OK' : 'OK',
              langEn: lang,
              onOk: function () {
                // click ok
              }
            })
          }
          return
        }
      }
      // 把贴图ID编译话
      var stampIds = ''
      // let stampSetArry = []
      if (stampSetArry) {
        if (stampSetArry.length > 0) {
          for (let j = 0; j < stampSetArry.length; j++) {
            stampIds += stampSetArry[j] + '|'
          }
        }
      }
      const basic = stampIds.substring(0, stampIds.lastIndexOf('|'))
      const copryright = MapData.flag_print_copyright === 'OK' ? MapData.copyright : ''
      // let fabricJSON = canvas.toDatalessJSON()
      // let layerslist = changeLayers('', { fabric: fabricJSON, data: toDataurl, stampIds: basic, copyRight: copryright, flagList: MapData.flag_list }, layers)
      // let layerslist = changeLayers('', { data: toDataurl, flag_can_other_layer_over: flag_can_other_layer_over, stampIds: basic, copyRight: copryright, flagList: MapData.flag_list }, layers)
      const lastLayer = layers[layers.length - 1]
      lastLayer.flag_can_other_layer_over = flag_can_other_layer_over
      lastLayer.copyRight = copryright
      lastLayer.stampIds = basic
      lastLayer.flagList = MapData.flag_list

      imageTools.storeImageData({ imageName: `front-${layers.length}`, imageData: toDataurl })
      await invoke('setLayers', layers)
      invoke('navigateTo', {
        to: '/mixChoice',
        params: { itemModal: 'show', previousPage }
      })
      initialization()
      setShowTypeset(false)
    } catch (error) {
      // TODO 处理错误
      setAlertModelVisible(true)
    }
  }
  function initialization () {
    firstSlectMap = true
    // MapData = {}
    firstSignNum = 0
    nums = 0
    imgOverFlow = false
    flag_can_other_layer_over = ''
    delay = null
    delayType = true
    overLap = null
  }
  function goPage () {
    if (delayType) {
      delayType = false
      toJson()
      delay = setTimeout(() => {
        delayType = true
        window.clearTimeout(delay)
      }, (2000))
    }
  }

  return (
    <>

      <div className='simple-trans-main'>
        <Navbar pageMark='setStamp' page='setStamp' currentPage='' revoke={() => {
          change()
        }} />
        <ViewPort topLayer={props.topLayer} data={props.mixData} noMultiply>
          <div style={{ position: 'absolute', top: 0, left: 0 }}>
            <canvas ref={canvasRef} id={'setStamp_canvas'} width={canvasSize} height={canvasSize} />
          </div>
          {
            !jiuVisible ? null : (
              <img style={{ pointerEvents: 'none', position: 'absolute', width: areaStyle.width + 4, height: areaStyle.height + 3.5, left: areaStyle.left - 2, top: areaStyle.top - 2 }} src={editArea} alt='' />
            )
          }
          <div className={!maskType ? `mask` : ''} style={{ width: '100%', height: '100%' }} />
        </ViewPort>
        <Toolbar>
          <div style={{
            borderRadius: 25,
            width: 50,
            height: 50,
            position: 'relative'
          }}>
            <img className='stampEnables' src={stamp} onClick={() => goStampShowcase()} style={{ zIndex: 1 }} />
            <div
              ref={addMapAlertRef}
              id='addMapAlert' style={{
                position: 'absolute',
                left: -10
              }} />
          </div>
          {
            textType ? null : (
              <div className={`text ${local.current === 'en' ? 'smilingFaceTextEn' : 'smilingFaceTextJa'}`} style={{ background: `url(${textBoxIconBackground}) no-repeat` }}>
                <Locale code='make.home.stamp.addMapalertText' />
              </div>
            )
          }

          <Enlarge attribute={enlargeAttr} />
        </Toolbar>

        <img id='deleteImg'
          ref={deleteImgRef}
          src={deleteImg}
          alt='删除图标'
          style={{ width: deleteIconWidth, height: deleteIconHeight, position: 'fixed', zIndex: 9999, display: 'none', pointerEvents: 'none' }} />
        <AddMap
          onRef={(e) => { fontComponent.current = e }}
          delRef={(e) => { deleteObj.current = e }}
          onSelectStamp={onSelectStamp} />
        <div onClick={() => goPage()}>
          <img
            id='GotoMixIcon'
            ref={GotoMixIconRef}
            src={GotoMixIcon}
            className='GotoMixIcon'
            alt='go to thie mix page'
            style={{
              position: 'absolute',
              right: 12,
              top: 2,
              zIndex: 11,
              // width: 38,
              display: 'none'
            // height: 38,
            }} />
        </div>
        <img
          id='nextDisabel'
          ref={nextDisabelRef}
          src={nextDisabel}
          className='GotoMixIcon'
          alt='go to thie mix page'
          style={{
            position: 'absolute',
            right: 12,
            top: 2,
            zIndex: 11,
            display: 'block'
          // width: 40,
          // height: 40,
          }} />
        {
          showTypeset ? <TypesetPrompt moveIcon='Stamp_Drag.gif'
            resizeIcon='Stamp_Resize.gif'
            title='make.home.stamp.resize.title'
            hideTypesetProps={() => setShowTypeset(false)} /> : ''
        }
        <RevokeComponent pageMark={'paint'} cancelDesign={() => cancelDesign()} show={showStatus} setShow={(status) => setShowStatus(status)} />
      </div>
      <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => { setAlertModelVisible(false) }} />
</>
  )
}

export default injectImages()(SetStamp)
