
export const Parameter = (val1, val2, val3) => {
  var s = ''
  var d = new Date()
  var m = d.getUTCMonth() + 1
  s += String(d.getUTCFullYear())
  s += (m < 10 ? '0' + String(m) : String(m))
  s += (d.getUTCDate() < 10 ? '0' + String(d.getUTCDate()) : String(d.getUTCDate()))
  s = val3 + s
  s = val2 + s
  s = val1 + s
  return s
}

export default Parameter
