/*
 * 轻提示
 */
import invoke from '../utils/invoke'
export const ljh_toast = (e) => { // react/vue 里使用时打开
// ljh_toast = (e) => { //正常使用
  // 初始化参数
  var time

  var htm = ''

  var htm_child = ''

  var g_t = 2
  // 接受到的参数
  var t = e.timeOut

  var c = e.content
  // 判断是否传销毁时间
  if (t) {
    time = t * 1000
  } else {
    time = g_t * 1000
  }
  // 创建主题
  if (c) {
    htm = document.createElement('div')
    htm.className = 'toast'
    htm.id = 'toast'
    htm_child += '<p>' + c + '</p>'
    htm.innerHTML = htm_child
  }
  // 添加到页面body中
  document.body.appendChild(htm)
  // 延时销毁创建的主题
  setTimeout(() => {
    document.getElementById('toast').remove()
  }, time)
}
/*
 * 弹出层
 */
export const ljh_popups = (e) => { // react/vue 里使用时打开
// ljh_popups = (e) => { //正常使用
  // 初始化参数
  var time = 200

  var htm = ''

  var htm_child = ''

  var mb

  var next

  var cancel

  var d_v

  var value = ''

  var bt1

  var bt2

  var nav

  var zh_v

  var mm_v

  var layers

  var list_type

  var control_mask = false

  var w_width = window.innerWidth

  var w_height = window.innerHeight
  // 接受到的参数
  var type = e.type
  var lang = e.langEn

  var title = e.title

  var lobal = e.lobal

  var zh = e.zh

  var mm = e.mm

  var zh_text = e.zh_text

  var mm_text = e.mm_text

  var zh_type = e.zh_type

  var mm_type = e.mm_type

  var input_type = e.input_type

  var content = e.content

  var prompt_text = e.prompt_text

  var next_text = e.next_text

  var cancel_text = e.cancel_text

  var list = e.list

  var buttons = e.buttons

  var s_one = e.s_one

  var s_two = e.s_two

  var onOk = e.onOk

  var onOff = e.onOff

  var on_bt = e.on_bt

  var imgUrl = e.imgUrl
  var describe = e.describe

  // 检查传过来的参数是否全，缺少的部分补上默认的文字。
  if (e) {
    if (!type) {
      ljh_toast({ content: 'type类型为必填项，请补全参数', timeOut: 2 })
      return
    }
    if (type == 'alert') {
      if (!title) title = ''
      if (!content) content = '我是内容'
      if (!next_text) next_text = '确定'
    } else if (type == 'confirm') {
      if (!title) title = ''
      if (!content) content = '我是内容'
      if (!next_text) next_text = '确定'
      if (!cancel_text) cancel_text = '取消'
    } else if (type == 'c_Input') {
      if (!title) title = '提示'
      if (!lobal) lobal = '表单标头：'
      if (!input_type) input_type = 'text：'
      if (!prompt_text) prompt_text = '请输入xxxx'
      if (!next_text) next_text = '确定'
      if (!cancel_text) cancel_text = '取消'
    } else if (type == 'c_Type') {
      if (!s_one) s_one = 'one'
      if (!s_two) s_two = 'two'
    } else if (type == 'c_bt_list') {
      if (!title) title = '提示'
      if (!content) content = '我是内容'
      // console.log(buttons.length)
      if (buttons.length == 0) {
        buttons = [{ name: '按钮1', id: '1' }, { name: '按钮2', id: '2' }]
      }
    } else if (type == 'c_login') {
      if (!title) title = '登录'
      if (!zh) zh = '账号'
      if (!mm) mm = '密码'
      if (!zh_type) zh_type = 'text'
      if (mm_type) mm_type = 'password'
      if (!zh_text) zh_text = '请输入账号'
      if (!mm_text) mm_text = '请输入密码'
      if (!next_text) next_text = '登录'
      if (!cancel_text) cancel_text = '取消'
    } else if (type == 'f_layer') {
      if (!title) title = '提示'
      if (list.length == 0) {
        list = [{ name: '列表1', id: '1' }, { name: '列表2', id: '2' }, { name: '列表3', id: '3' }]
      }
      if (!next_text) next_text = '确定'
      if (!cancel_text) cancel_text = '取消'
    }
  }
  // 创建主题
  if (type) {
    htm = document.createElement('div')
    if (type === 'f_layer') {
      htm.className = 'layers'
      htm.id = 'layers'
      htm_child += "<div class='layer_wrap'>"
    } else {
      htm.className = 'popups'
      htm.id = 'popups'
      htm_child += "<div id='mengban' class='mengban'></div>"
      if (type === 'prohibit') {
        htm_child += "<div class='wrap' style='width:" + (w_width - 24) + "px;margin:-127px 12px 0 12px;background-color: #FFFFFF;' >"
      } else if (type === 'confirmTH') {
        const chazhi = (w_width - (w_width * 0.76)) / 2
        htm_child += `<div class='wrap' style='width:${w_width * 0.76}px;margin:-93px ${chazhi}px 0 ${chazhi}px;border-radius: 12px;'>`
      } else if (type === 'overlap') {
        const chazhi = (w_width - (w_width * 0.76)) / 2
        htm_child += `<div class='overlapWrap' style='width:${w_width * 0.76}px;margin:-93px ${chazhi}px 0 ${chazhi}px;border-radius: 12px;'>`
      }
    }

    // 头部
    if (type == 'alert' || type == 'confirm' || type == 'c_bt_list' || type == 'c_login') {
      if (title) {
        htm_child += "<p class='title'>" + title + '</p>'
      }
    } else if (type == 'c_Input') {
      htm_child += "<p class='title'>" + title + '</p>'
    } else if (type == 'f_layer') {
      htm_child += "<p class='layer_title'>" + title + '</p>'
    }

    // 中部
    if (type == 'alert' || type == 'confirm' || type == 'c_bt_list') {
      htm_child += "<p class='contents'>" + content + '</p>'
    } else if (type == 'c_Input') {
      htm_child += "<div class='content_input'>"
      htm_child += "<p class='lobal'>" + lobal + '</p>'
      htm_child += "<input id='d_v' type='" + input_type + "' placeholder='" + prompt_text + "'>"
      htm_child += '</div>'
    } else if (type == 'c_Type') {
      htm_child += "<div class='c_Type'>"
      htm_child += "<p id='bt1' class='s_one_text'>" + s_one + '</p>'
      htm_child += "<p id='bt2' class='s_two_text'>" + s_two + '</p>'
      htm_child += '</div>'
    } else if (type == 'c_login') {
      htm_child += "<div class='c_login'>"
      htm_child += "<p class='zh'>" + zh + '</p>'
      htm_child += "<input id='zh_v' type='" + zh_type + "' placeholder='" + zh_text + "'>"
      htm_child += "<p class='mm'>" + mm + '</p>'
      htm_child += "<input id='mm_v' type='" + mm_type + "' placeholder='" + mm_text + "'>"
      htm_child += '</div>'
    } else if (type == 'f_layer') {
      htm_child += "<ul id='nav' class='layer_list'>"
      list.map((v, k) => {
        htm_child += '<li>' + v.name + '</li>'
      })
      htm_child += '</ul>'
    } else if (type == 'prohibit') {
      htm_child += lang !== 'en' ? "<div class='proImgWrap'><img class='proImg' src='" + imgUrl + "' alt='icon'/></div>" : "<div class='proImgWrapEn'><img class='proImg' src='" + imgUrl + "' alt='icon'/></div>"
      htm_child += lang !== 'en' ? "<p class='proContent'>" + content + '</p>' : "<p class='proContentEn'>" + content + '</p>'
    } else if (type === 'confirmTH' || type === 'overlap') {
      htm_child += `<p class='${lang === 'en' ? 'THcontentEn' : 'THcontentJa'}'>${content}</p>`
      htm_child += `<p class='${lang === 'en' ? 'THdescribeEn' : 'THdescribeJa'}'> ${describe}</p>`
      htm_child += `<div class='hengxian'></div>`
    }

    // 底部
    if (type == 'alert') {
      htm_child += "<div class='f_alert'>"
      htm_child += "<p id='next'>" + next_text + '</p>'
      htm_child += '</div>'
    } else if (type == 'confirm' || type == 'c_Input' || type == 'c_login') {
      htm_child += "<div class='f_confirm'>"
      htm_child += "<p id='cancel' class='cancel_text'>" + cancel_text + '</p>'
      htm_child += "<p id='next' class='next_text'>" + next_text + '</p>'
      htm_child += '</div>'
    } else if (type == 'c_bt_list') {
      htm_child += "<ul id='nav' class='c_bt_list'>"
      buttons.map((v, k) => {
        htm_child += '<li>' + v.name + '</li>'
      })
      htm_child += '</ul>'
    } else if (type == 'f_layer') {
      htm_child += "<p id='cancel' class='cancel_layer'>" + cancel_text + '</p>'
    } else if (type == 'prohibit') {
      htm_child += "<div class='pro_footer' id='next'>" + next_text + '</div>'
    } else if (type === 'confirmTH') {
      htm_child += "<div class='th_confirm'>"
      htm_child += `<p id='cancel' class='${lang === 'en' ? 'cancel_text_en' : 'cancel_text_ja'}'>${cancel_text}</p>`
      htm_child += `<div class='shuzhiLine'></div>`
      htm_child += `<p id='next' class='${lang === 'en' ? 'next_text_en' : 'next_text_ja'}'>${next_text}</p>`
      htm_child += '</div>'
    } else if (type === 'overlap') {
      htm_child += "<div class='overlap'>"
      htm_child += "<p id='next' class='next_text'>" + next_text + '</p>'
      htm_child += '</div>'
    }

    htm_child += '</div>'
    htm.innerHTML = htm_child
  }
  // 添加到页面body中
  document.body.appendChild(htm)
  // 控制蒙版销毁主题
  if (type == 'f_layer') {
    mb = document.getElementById('layers')
    mb.onclick = function () {
      if (control_mask == true) {
        setTimeout(() => {
          mb.remove()
        }, time)
      }
    }
  } else {
    mb = document.getElementById('popups')
    mb.onclick = function () {
      if (control_mask == true) {
        setTimeout(() => {
          mb.remove()
        }, time)
      }
    }
  }
  // 点击蒙版销毁弹窗
  var mengban = document.getElementById('mengban')
  if (mengban) {
    mengban.onclick = function () {
      mb.remove()
    }
  }

  // 点击确定
  next = document.getElementById('next')
  d_v = document.getElementById('d_v')
  zh_v = document.getElementById('zh_v')
  mm_v = document.getElementById('mm_v')
  if (next) {
    next.onclick = function () {
      if (type == 'c_Input') {
        value = d_v.value
        setTimeout(() => {
          mb.remove()
          onOk(value)
        }, time)
      } else if (type == 'c_login') {
        let zh = zh_v.value
        let mm = mm_v.value
        setTimeout(() => {
          mb.remove()
          onOk(zh, mm)
        }, time)
      } else {
        setTimeout(() => {
          mb.remove()
          onOk(value)
        }, time)
      }
    }
  }
  // 点击取消
  cancel = document.getElementById('cancel')
  if (cancel) {
    cancel.onclick = function () {
      setTimeout(() => {
        mb.remove()
        onOff()
      }, time)
    }
  }
  // 弹出层c_Type
  bt1 = document.getElementById('bt1')
  bt2 = document.getElementById('bt2')
  if (bt1) {
    bt1.onclick = function () {
      setTimeout(() => {
        mb.remove()
        onOk()
      }, time)
    }
  }
  if (bt2) {
    bt2.onclick = function () {
      setTimeout(() => {
        mb.remove()
        onOff()
      }, time)
    }
  }
  // 弹出层c_bt_list || f_layer
  nav = document.getElementById('nav')
  if (nav) {
    if (type == 'c_bt_list') {
      list_type = buttons.length
    } else if (type == 'f_layer') {
      list_type = list.length
    }
    for (let i = 0; i < list_type; i++) {
      nav.childNodes[i].onclick = function () {
        var data
        if (type == 'c_bt_list') {
          data = buttons[i]
        } else if (type == 'f_layer') {
          data = list[i]
        }
        setTimeout(() => {
          mb.remove()
          on_bt(data)
        }, time)
      }
    }
  }
  // 弹出层c_bt_list
}

export default { ljh_toast, ljh_popups }// react/vue 里使用时打开s
